import { AsyncPipe, DatePipe, TitleCasePipe } from '@angular/common';
import { Component, inject, computed } from '@angular/core';
import { Params, RouterModule } from '@angular/router';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TranslateModule } from '@ngx-translate/core';
import { Store } from '@ngxs/store';
import { Observable } from 'rxjs';

import { AddressModal } from '../../../shared/components/widgets/modal/address-modal/address-modal';
import { DeleteAddressModal } from '../../../shared/components/widgets/modal/delete-address-modal/delete-address-modal';
import { NoData } from '../../../shared/components/widgets/no-data/no-data';
import { Pagination } from '../../../shared/components/widgets/pagination/pagination';
import { IAccountUser } from '../../../shared/interface/account.interface';
import { IOrderModel } from '../../../shared/interface/order.interface';
import { IUserAddress } from '../../../shared/interface/user.interface';
import { CurrencySymbolPipe } from '../../../shared/pipe/currency.pipe';
import { AccountState } from '../../../shared/store/state/account.state';
import { GetOrdersAction } from '../../../shared/store/action/order.action';
import { OrderState } from '../../../shared/store/state/order.state';
import { toSignal } from '@angular/core/rxjs-interop';

@Component({
  selector: 'app-orders',
  imports: [
    TranslateModule,
    RouterModule,
    CurrencySymbolPipe,
    Pagination,
    NoData,
    AsyncPipe,
    DatePipe,
    TitleCasePipe,
  ],
  templateUrl: './orders.html',
  styleUrl: './orders.scss',
})
export class Orders {
  private store = inject(Store);
  private modal = inject(NgbModal);

  order$: Observable<IOrderModel> = inject(Store).select(
    OrderState.order,
  ) as Observable<IOrderModel>;

  user$: Observable<IAccountUser> = inject(Store).select(
    AccountState.user,
  ) as Observable<IAccountUser>;

  private orderSignal = toSignal(this.order$);

  refundedOrders = computed(() => {
    const orders = this.orderSignal()?.data ?? [];
    return orders.filter(o => o.payment_status?.toLowerCase() === 'refunded');
  });

  public filter: Params = {
    page: 1,
    paginate: 10,
  };

  constructor() {
    this.store.dispatch(new GetOrdersAction(this.filter));
  }

  setPaginate(page: number) {
    this.filter['page'] = page;
    this.store.dispatch(new GetOrdersAction(this.filter));
  }

  openAddressModal(address?: IUserAddress) {
    const modal = this.modal.open(AddressModal, { centered: true, windowClass: 'theme-modal-2' });
    if (address) {
      modal.componentInstance.userAddress = address;
    }
  }

  removeAddress(address: IUserAddress) {
    this.modal.open(DeleteAddressModal, { centered: true }).componentInstance.userAddress = address;
  }
}
